main .main {
  width: 90%;
  margin: 10% auto;
  border-radius: 15px;
  background: white;
}
@media (min-width: 71.25em) {
  main .main {
    display: flex;
    flex-direction: row;
    width: 50%;
    margin: 10% auto;
  }
}
main .main__image img {
  object-fit: cover;
  max-width: 100%;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}
@media (min-width: 71.25em) {
  main .main__image img {
    border-bottom-left-radius: 15px;
    border-top-left-radius: 15px;
    border-top-right-radius: 0;
    margin-right: 20px;
  }
}
@media (min-width: 71.25em) {
  main .main__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
main .main__heading {
  font-weight: 700;
  margin: 1.875rem 0 0.9375rem 0;
  font-size: 0.6875rem;
  color: var(--veryDarkGrayishBlue);
  text-align: left;
  padding-inline: 0.9375rem;
}
@media (min-width: 71.25em) {
  main .main__heading {
    text-align-last: left;
    padding-inline: 1.25rem;
  }
}
main .main__paragraph {
  font-weight: 400;
  text-align: left;
  color: var(--DeasaturatedDarkBlue);
  line-height: 1.5;
  padding-inline: 0.9375rem;
}
@media (min-width: 71.25em) {
  main .main__paragraph {
    text-align-last: left;
    white-space: pre-line;
    padding-inline: 1.25rem;
  }
}
main .main__creator {
  padding-block: 0.9375rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: relative;
  padding-inline: 0.9375rem;
}
@media (min-width: 71.25em) {
  main .main__creator {
    padding: 1.25rem 1.25rem 0 1.25rem;
    padding-block: 0;
  }
}
main .main__creator--avatar {
  position: relative;
}
main .main__creator--avatar img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
  border-radius: 50%;
}
main .main__creator--name {
  font-weight: 300;
  color: var(--veryDarkGrayishBlue);
  padding: 10px;
  font-weight: 600;
  flex-grow: 2;
}
main .main__creator--name span {
  padding: 0;
  opacity: 0.8;
  font-weight: 400;
}
main .main__creator--share {
  margin: 0.625rem;
  text-align: center;
  padding: 3px;
}
@media (min-width: 71.25em) {
  main .main__creator--share {
    position: relative;
  }
}
main .main__creator--share .button {
  border: none;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  cursor: pointer;
  position: absolute;
  z-index: 1;
  top: 24px;
  bottom: 0;
  right: 20px;
  display: block;
}
@media (min-width: 71.25em) {
  main .main__creator--share .button {
    top: -15px;
    right: 0;
  }
}
main .main__creator--share .button img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}
main .main__creator--share .top {
  top: 33px;
}
@media (min-width: 71.25em) {
  main .main__creator--share .top {
    top: -15px;
  }
}
main .main__creator--popup {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0.625rem 1.5625rem;
  position: absolute;
  visibility: hidden;
  top: 20px;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  background: var(--veryDarkGrayishBlue);
}
@media (min-width: 71.25em) {
  main .main__creator--popup {
    border-radius: 0.625rem;
    height: 55px;
    top: -72px;
    left: 58%;
    max-width: 58%;
    justify-content: space-between;
    box-shadow: 0px 10px 10px 0px rgba(0, 0, 0, 0.1);
  }
  main .main__creator--popup::after {
    content: "";
    position: absolute;
    width: 1.6rem;
    height: 1.6rem;
    bottom: -0.8rem;
    left: 50%;
    transform: rotate(45deg);
    background-color: var(--veryDarkGrayishBlue);
    box-shadow: 0px 15px 10px -3px rgba(0, 0, 0, 0.1);
  }
}
main .main__creator--popup p {
  text-transform: uppercase;
  letter-spacing: 5px;
  padding: 0.3125rem;
  color: var(--grayishBlue);
}
@media (min-width: 71.25em) {
  main .main__creator--popup p {
    padding: 0.625rem;
  }
}
main .main__creator--popup ul {
  list-style-type: none;
  display: flex;
  align-items: center;
  margin-left: 0.625rem;
}
@media (min-width: 71.25em) {
  main .main__creator--popup ul {
    margin-left: 0;
  }
}
main .main__creator--popup ul li {
  padding-right: 0.9375rem;
}
@media (min-width: 71.25em) {
  main .main__creator--popup ul li {
    padding-right: 0.9375rem;
  }
}
main .main__creator .show {
  visibility: visible;
  opacity: 1;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-Manrope);
  background: var(--lightGrayishBlue);
  font-size: 0.8125rem;
}

:root {
  --veryDarkGrayishBlue: hsl(217, 19%, 35%);
  --DeasaturatedDarkBlue: hsl(214, 17%, 51%);
  --grayishBlue: hsl(212, 23%, 69%);
  --lightGrayishBlue: hsl(210, 46%, 95%);
}

:root {
  --font-Manrope: "Manrope", sans-serif;
}